import React, { Component } from "react";
import FooterOnly from "~/layouts/FooterOnly/FooterOnly.js";
import Tabs from "~/components/tabs/tabs";
import "~/pages/paymentmethod/paymentmethod.css";

class PaymentMethod extends Component {
  render() {
    return (
      <FooterOnly>
        <div className="paymentmethod">
          <div className="headerPayment">
            <a href="# " className="iconBack">
              <i class="bi bi-chevron-left"></i>
            </a>
            <div className="title text-center">
              Phng thc thanh ton ca ti
            </div>
          </div>
          <div className="showBank d-flex flex-column">
            <div className="nameBank">
              <span>DongA Bank</span>
            </div>
            <div className="number">
              <span>092****226</span>
            </div>
            <div className="close">
              <span></span>
            </div>
          </div>
          <a href="# " className=" addBank text-center d-block">
            Thm th ngn hng
          </a>
          <div className="note">
            Mo: Nu khng tm thy th ngn hng trc ,  l do nn tng
            thanh ton  c chuyn i v th ngn hng cn c thm li.
          </div>
        </div>
      </FooterOnly>
    );
  }
}

export default PaymentMethod;
